.Blog {
    width: 80%;
    margin: 0 auto;
    padding-bottom: 1em; 
}
.Blog ul {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: center;
}

.Blog li {
    display:inline-block;
    margin: 20px

}

.Blog a {
    text-decoration: none;
    color:#CDCDCD;
}

.Blog a:hover,
.Blog a.active {
    color: #fa923f
}


